img {
    width: 100%;
  }
  
  .homeLanding {
    position: relative;
    background: url("./showcase.jpeg") no-repeat;
    background-size: cover;
    background-position: center;
    height: 86.7vh;
    margin-top: -24px;
    margin-bottom: -50px;
    overflow:hidden;
  }
  .landing {
    position: relative;
    /* background: url("./showcase.jpeg") no-repeat; */
    background-size: cover;
    background-position: center;
    height: 86.7vh;
    margin-top: -24px;
    margin-bottom: -50px;
    overflow:hidden;
  }
  
  .landing-inner {
    padding-top: 80px;
  }
   
  .dark-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .card-form {
